import React from 'react';
import { Button, Popconfirm, Icon } from 'antd';
import { connect } from 'react-redux';
import * as creators from '../store/creators';
import { withRouter } from 'react-router-dom';

function Oper(props) {
  const fontSmall = { fontSize: "12px" };
  return (
    <div>
      <Button
        onClick={() => props.changeAddModalvisible(true, "edit", props.record)}
        style={fontSmall}
        type="primary"
        size="small"
        ghost
      ></Button>&nbsp;&nbsp;
      <Popconfirm
        placement="left"
        title="?"
        onConfirm={() => {
          const data = "id=" + props.record.type
          props.deleteType({ props, data })
        }}
        okText=""
        icon={<Icon type="question-circle" />}
        cancelText="">
        <Button style={fontSmall} type="danger" size="small" ghost></Button>
      </Popconfirm>&nbsp;&nbsp;
    </div >
  )
}

const mapState = state => ({
  params: state.dictType.params,
  isQuery: state.dictType.isQuery
})

const mapDispatch = dispatch => ({
  changeAddModalvisible: (addModalvisible, operationType, record) => {
    const action = creators.changeAddModalvisibleAction(addModalvisible, operationType, record);
    dispatch(action);
  },
  deleteType: req => {
    const action = creators.deleteDictTypeAction(req);
    dispatch(action);
  },
})

export default withRouter(connect(mapState, mapDispatch)(Oper));
